.todo-container{
    background: black;
    width: 90vw;
    margin: 5em  auto;
    border-radius: 10px;
    padding: 15px;
    color: white;
}
.header{
    font-size: 2.5rem;
    font-weight: bold;
    text-align: center;

}
.input-container{
    background: black;
    padding: 0.5em;
    border-radius: 1em;
    border: 2px solid #11B0AB;
}
.input{
    font-size: 1.5em;
    font-weight: bold;
    color: black;
    background-color:  white;
    border: 1px;
    border-radius: 1em;
    padding: 0.5em;
    margin: 1em;
    width: 80%;

}
.add-btn{
    border-radius:5px;
    font-size: 2em;
    margin: 2px;
    padding: 0 15px;
    border: none;
    cursor: pointer;
    color: black;
    background-color: #14CECA;
}
.list-tasks{
    font-size: 1.2em;
    font-weight: bold;
    font-style: italic;
    color:white;
    background-color: black;
    border: 1px solid #11B0AB;
    border-radius: 5px;
    padding: 0.5em;
    margin: 0.5em;
}
.delete-btn{
    border-radius: 5px;
    margin: 0px 5px;
    padding: 3px 5px;
    border: none;
    cursor: pointer;
    color: black;
    float: right;
    background-color:#14CECA;
}